.card {
  text-align: center;
  perspective: 1000px;
  border-radius: 5px;
  width: 308px;
  height: 560px;
  display: inline-block;
}

body {
  background: url("background.b3d5e25f.png") 0 0 / cover no-repeat fixed;
}

.champion_name {
  text-align: center;
  perspective: 1000px;
  color: #fff;
  -webkit-user-select: none;
  border-radius: 5px;
  width: 308px;
  height: 620px;
  font-family: Beaufort for LOL;
  display: inline-block;
}

.logo, .btn {
  justify-content: center;
  display: flex;
}

.card-inner {
  cursor: pointer;
  transform-style: preserve-3d;
  transform-origin: 100%;
  width: 100%;
  height: 100%;
  transition: transform 1s;
  position: relative;
}

.champion_name:hover {
  cursor: pointer;
}

.card-inner.is-flipped {
  transform: translateX(-100%)rotateY(-180deg);
}

.card-front, .card-back {
  color: #fff;
  text-align: center;
  backface-visibility: hidden;
  z-index: 1;
  width: 100%;
  height: 100%;
  font-weight: bold;
  position: absolute;
}

.card-back {
  color: #c89b3c;
  background-color: #091428;
  transform: rotateY(180deg);
  box-shadow: inset 10px 10px 50px 10px #000;
}

.blurb {
  text-align: center;
  height: 100%;
  margin-top: 150px;
  margin-left: 30px;
  margin-right: 30px;
  font-size: 20px;
}

@font-face {
  font-family: Beaufort for LOL;
  src: url("BeaufortforLOL-Italic.e4cc718d.ttf");
}

.button {
  cursor: pointer;
  z-index: 1;
  letter-spacing: 3px;
  text-transform: uppercase;
  color: #937341;
  box-sizing: inherit;
  -webkit-user-select: none;
  user-select: none;
  background: linear-gradient(#ecc572 0, #815500);
  border: none;
  min-width: 240px;
  max-width: 100%;
  padding: 23px 25px;
  font-family: Beaufort for LOL;
  font-size: 1.8rem;
  font-weight: 400;
  line-height: 1;
  transition: color .2s;
  position: relative;
  box-shadow: 0 0 25px #0000001c;
}

.button:before {
  content: "";
  z-index: -1;
  background: #111;
  display: block;
  position: absolute;
  inset: 2px;
}

.name:hover {
  color: #c89b3c;
  width: 250px;
  transition: width .5s;
}

.name {
  background-color: #091428;
  border: 1px solid #c89b3c;
  width: 208px;
  height: 65px;
  margin: auto;
  transform: skew(-20deg);
}

.loader {
  z-index: 0;
  visibility: hidden;
  border: 16px solid #f3f3f3;
  border-top-color: #3498db;
  border-radius: 50%;
  width: 120px;
  height: 120px;
  margin-top: 160px;
  margin-left: 100px;
  animation: 2s linear infinite spin;
  position: absolute;
}

@keyframes spin {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}
